<template>
    <div>
        <div class="text-center mt-2 mb-4">
           <span class="main-color" style="font-size: 20px">ເລືອກໝວດໝູ່ວຽກທີ່ຕ້ອງການ</span>
        </div>
        <app-category :loading="loading" :categories="categories"></app-category>
    </div>
</template>

<script>
    import appCategory from '../../components/category/Category'
    export default {
        components: {
          appCategory
        },
        data: () => ({
            categories: [],
            loading: false
        }),
        methods: {
          async fetchCategories () {
              this.loading = true
              const results = await this.$axios.get('category/getall')
              this.categories = results.data.categories
              setTimeout(() => {
                  this.loading = false
              }, 200)
           }
        },
        created () {
            this.fetchCategories()
        }
    }
</script>

